import MescrollUni from 'mescroll-uni'
import Panel from '@/components/common/Panel.vue'
import ArticleItem from './ArticleItem.vue'
import ArticleCell from './ArticleCell.vue'

export default {
	components: {
		MescrollUni: MescrollUni,
		Panel: Panel,
		ArticleItem: ArticleItem,
		ArticleCell: ArticleCell
	},
	props: {
		pageIndex: {
			type:Number,
			default:function(){
				return 0
			}
		},
		// 类别id
		cateId: {
			type: Number,
			default: function() {
				return 0
			}
		}
	},
	data() {
		return {
			menus: [],
			flexDatas: [],
			mescrolls: [],
			downOptions: {
				auto: false
			},
			upOptions: {
				auto: false,
				textNoMore: '无更多数据'
			},
			articles: []
		}
	},
	methods: {
		// 初始化mescroll
		initMescrolls(mescroll) {
			this.$emit('init', {
				mescroll,
				pageIndex:this.pageIndex
			})
		},
		// 下拉刷新
		downCallback(mescroll) {
			console.log(this.cateId)
			this.$uRequest.get({
				url: '/mini/home/index',
				data: {
					cate_id: this.cateId
				},
				success: (res) => {
					if (res.code === 0) {
						this.flexDatas.splice(0, this.flexDatas.length, ...res.data)
						mescroll.endSuccess(this.flexDatas.length, true)
						// 触发上拉动
						mescroll.triggerUpScroll()
					} else {
						mescroll.endErr()
					}
				},
				fail: (err) => {
					console.log(JSON.stringify(err))
					mescroll.endErr()
				}
			})
		},
		// 上拉刷新
		upCallback(mescroll) {
			console.log('加载更多', mescroll.num)
			this.$uRequest.get({
				url: '/mini/home/articles',
				data: {
					cate_id: this.cateId
				},
				success: (res) => {
					if (res.code === 0) {
						this.articles.splice(this.articles.length, 0, ...res.data.items)
						mescroll.endBySize(res.data.items.length, res.data.total)
					} else {
						mescroll.endErr()
					}
				},
				fail: (err) => {
					console.log(JSON.stringify(err))
					mescroll.endErr()
				}
			})
		}
	}
}
